import React from 'react';
import PropTypes from 'prop-types';
import './styles.css';

const BorUiButton = ({ children, onClick, type, className }) => {
  const getClassName = type => {
    let btnClass = 'bor-ui-button';
    if (type === '+') {
      btnClass += ' bor-ui-button-positive';
    } else if (type === '-') {
      btnClass += ' bor-ui-button-negative';
    }
    if (className) {
      btnClass += ' ' + className;
    }
    return btnClass;
  };
  return (
    <button onClick={onClick} className={getClassName(type)} type="button">
      {children}
    </button>
  );
};

BorUiButton.displayName = 'BorUiButton';
BorUiButton.propTypes = {
  children: PropTypes.node.isRequired,
  onClick: PropTypes.func
};
BorUiButton.defaultProps = {
  onClick: () => {}
};

export default BorUiButton;
